.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  height: 100px;
  z-index: 50;
  background-color: white;
  display: flex;
  align-items: center;
  padding: 0 25px;
  @include phone {
    padding: 0 15px; }
  .menuBtn {
    @include bg('menuBtn.png');
    cursor: pointer;
    @include dTime;
    &.cloz {
      @include bgi('menuBtnCloz.png'); }
    @include phone {
      transform: scale(.7);
      @include bg('menuBtn2.png');
      &.cloz {
        transform: scale(.8);
        @include bg('menuBtnCloz2.png'); } } }

  .logo {
    @include bg('logo.png');
    position: absolute;
    left: 50%;
    transform: translateX(-50%); } }


